import React from "react";
import styled from "styled-components";

function About() {
  return (
    <Wrapper>
      <div className="containerDiv">  
        <span>About</span>
        <section>
        <div className="containerContent" >
          <div>Battery notifying app</div>
          <div>by - <a href='https://github.com/NileshSP/ReactJsBatteryNotifier'>github.com/NileshSP</a></div>
        </div>
        </section>
      </div>
    </Wrapper>
  );
}

const Wrapper = styled.div`
  margin:auto;
  height:100vh;
  background-color: #f76565;
  text-align:center;
  
  .containerDiv {
    padding:50px 15px 15px 15px;
  }

  span {
    top:20;
    color: #fff;
    font-size: 40px;
  }

  section {
    color: #fff;
  }

  .containerContent {
    margin:auto;
    width:100%;
    height:100%;
    border: solid 0px red;
  }

  .containerContent > div {
    padding:75px 15px 15px 15px;
    border: solid 0px lightgreen;
  }

  .containerContent > div > a {
    color:lightgreen;
  }

`;

export default About;
